<script>
// @ is an alias to /src
import Navbar from "@/components/Navbar.vue";
import Footer from "@/components/Footer.vue";
import RequestDemo from "@/components/RequestDemo.vue";

export default {
  name: "HomeView",
  components: {
    Navbar,
    Footer,
    RequestDemo,
  },
  data() {
    return {
      isActive: false,
      questEngineCarousel: [
        "img/home/figure-2.jpg",
        "img/quest-engine/figure-6.jpg",
      ],
      mintDropCarousel: [
        "img/mint-drop/figure-7.jpg",
        "img/mint-drop/figure-10.jpg",
      ],
      isLightBoxActive: false,
      lightBoxImageSrc: "",
    };
  },
  mounted() {
    this.track();
  },
  methods: {
    track() {
      this.$gtag.pageview("/");
      this.$gtag.query("event", "screen_view", {
        app_name: "zerodrop.io",
        screen_name: "Home",
      });
      this.$gtag.screenview({
        app_name: "zerodrop.io",
        screen_name: "Home",
      });
    },
    handleOpenActiveLightBox(src) {
      this.lightBoxImageSrc = src;
      this.isLightBoxActive = true;
    },
    handleCloseActiveLightBox() {
      this.lightBoxImageSrc = "";
      this.isLightBoxActive = false;
    },
  },
};
</script>
