<template>
  <section id="request-demo" class="section section-padding-none-touch">
    <div class="container">
      <div
        class="p-6 box has-background-black cust-border-color-dark is-border-none-touch"
        style="border: 1px solid"
      >
        <div class="has-text-centered">
          <p class="heading has-text-weight-bold gradient-text is-spaced">Request Demo</p>
          <p class="title is-size-2 is-size-4-touch has-text-weight-bold">
            Sign Up For A Demo And <br class="is-hidden-touch" />Start Engaging With Your Community!
          </p>
        </div>

        <form name="submit-to-google-sheet" class="field mt-6">
          <div class="columns">
            <div class="column">
              <div class="control has-icons-left has-icons-right">
                <input
                  class="input is-medium is-tablet-only duration-300"
                  name="Email"
                  type="email"
                  placeholder="Email"
                  required
                />
                <span class="icon is-medium is-left">
                  <ion-icon name="mail-outline" size="medium"></ion-icon>
                </span>
              </div>
              <div class="control has-icons-left has-icons-right mt-5">
                <input
                  class="input is-medium is-tablet-only duration-300"
                  name="Name"
                  type="text"
                  placeholder="Name"
                  required
                />
                <span class="icon is-medium is-left">
                  <ion-icon name="person-outline" size="medium"></ion-icon>
                </span>
              </div>
            </div>
            <div class="column">
              <div class="control">
                <textarea
                  class="textarea is-medium is-tablet-only duration-300"
                  placeholder="Messages"
                  name="Messages"
                  type="textarea"
                  rows="3"
                  required
                ></textarea>
              </div>
            </div>
          </div>

          <div class="field is-grouped is-grouped-centered">
            <button
              class="button is-primary is-medium is-rounded duration-300"
              type="submit"
            >
              Send
            </button>
          </div>
        </form>

        <div class="notification" id="successMsg" hidden>
          <button class="delete"></button>
          success
        </div>
      </div>
    </div>
  </section>
</template>

<script>
// @ is an alias to /src
export default {
  name: "RequestDemo",
  props: {
    msg: String,
  },
  data() {
    return {
      successMsg: false,
    };
  },
  components: {},
  mounted() {
    this.formSubmit();
  },
  methods: {
    formSubmit() {
      const scriptURL =
        "https://script.google.com/macros/s/AKfycbwNtasNFHrpVN2Gf0ftKgRFASHh9tn9IJAma0OAWQsKrbX4lCrNavtlAQjOYAIZN9WQRQ/exec";
      const form = document.forms["submit-to-google-sheet"];
      form.addEventListener("submit", (e) => {
        e.preventDefault();
        fetch(scriptURL, { method: "POST", body: new FormData(form) })
          .then(
            (response) => console.log("Success!", response),
            //show success message
            (this.submitSuccess())
          )
          .catch((error) => (this.submitFail()));
        //show error message
        form.reset();
      });
    },
    submitSuccess() {
      this.$buefy.toast.open({
        duration: 5000,
        message: `Woot! We will be in touch with you shortly.`,
        position: "is-bottom",
        type: "is-success",
      });
    },
    submitFail() {
      this.$buefy.toast.open({
        duration: 5000,
        message: `Oops, please try again later.`,
        position: "is-bottom",
        type: "is-danger",
      });
    },
  },
};
</script>

